import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";

const renderField = ({
  input,
  label,
  type,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input {...input} type={type} className="form-control" />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    switch (this.props.from) {
      case "titipan":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="langganan"
                  label="Langganan"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-3">
                <div className="form-group">
                  <label htmlFor="">Status</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="StatusTitipan"
                        component="input"
                        type="radio"
                        value="Open"
                        className="mr-2"
                      />
                      Open
                    </label>
                    <label>
                      <Field
                        name="StatusTitipan"
                        component="input"
                        type="radio"
                        value="Semua"
                        className="mr-2"
                      />
                      Semua
                    </label>
                  </div>
                </div>
              </div>
              <div
                className={
                  this.props.StatusTitipan === "Open"
                    ? "col-lg-3 d-none"
                    : "col-lg-3"
                }
              >
                <Field
                  name="tglFrom"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div
                className={
                  this.props.StatusTitipan === "Open"
                    ? "col-lg-3 d-none"
                    : "col-lg-3"
                }
              >
                <Field
                  name="tglTo"
                  label="Tanggal Akhir"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div
                className={
                  this.props.StatusTitipan === "Open"
                    ? "col-lg-3"
                    : "col-lg-3 d-none"
                }
              >
                <Field
                  name="tgl"
                  label="Tanggal Akhir"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "pembayaran":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="namaToko"
                  label="Nama Toko"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-3">
                <label htmlFor="">Jenis</label>
                <Field name="jenis" component="select" className="form-control">
                  <option value="SEMUA" key="SEMUA">
                    SEMUA
                  </option>
                </Field>
              </div>
              <div className="col-lg-3">
                <div className="form-group">
                  <label htmlFor="">.</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="StatusTitipan"
                        component="input"
                        type="checkbox"
                        value="Open"
                        className="mr-2"
                      />
                      Open
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "history":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <label htmlFor="">Tipe Laporan</label>
                <Field name="jenis" component="select" className="form-control">
                  <option value="NamaToko" key="NamaToko">
                    Nama Toko
                  </option>
                  <option value="NoBon" key="NoBon">
                    Nomor Bon
                  </option>
                </Field>
              </div>
              <div className="col-lg-3">
                <Field
                  name="namaToko"
                  label="Nama Toko"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="noBon"
                  label="Nomor Bon"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-3">
                <div className="form-group">
                  <label htmlFor="">.</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="jenisRekap"
                        component="input"
                        type="checkbox"
                        className="mr-2"
                      />
                      Rekap
                    </label>
                    <label className="mr-3">
                      <Field
                        name="jenisJualBeli"
                        component="input"
                        type="checkbox"
                        className="mr-2"
                      />
                      Jual/Beli LM
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sapai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "pembayaranSales":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <label htmlFor="">Sales</label>
                <Field name="jenis" component="select" className="form-control">
                  <option value="SEMUA" key="SEMUA">
                    SEMUA
                  </option>
                </Field>
              </div>
              <div className="col-lg-4">
                <label htmlFor="">Jenis</label>
                <Field name="jenis" component="select" className="form-control">
                  <option value="SEMUA" key="SEMUA">
                    Jenis
                  </option>
                </Field>
              </div>
              <div className="col-lg-4">
                <label htmlFor="">Status Validasi</label>
                <Field name="jenis" component="select" className="form-control">
                  <option value="OPEN" key="OPEN">
                    OPEN
                  </option>
                </Field>
              </div>
              <div className="col-lg-4">
                <div className="form-group">
                  <label htmlFor="">.</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="jenisTanggal"
                        component="input"
                        type="radio"
                        value="tanggal"
                        className="mr-2"
                      />
                      Tanggal Transaksi
                    </label>
                    <label className="mr-3">
                      <Field
                        name="jenisTanggal"
                        component="input"
                        type="radio"
                        value="tanggalInput"
                        className="mr-2"
                      />
                      Tanggal Input
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sapai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "return":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-6">
                <label htmlFor="">Jenis Laporan</label>
                <Field name="jenis" component="select" className="form-control">
                  <option value="namaToko" key="namaToko">
                    NAMA TOKO
                  </option>
                  <option value="SEMUA" key="SEMUA">
                    SEMUA
                  </option>
                </Field>
              </div>
              <div className="col-lg-6">
                <Field
                  name="namaToko"
                  label="Nama Toko"
                  className="form-control"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sapai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "gabung":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <Field
                  name="loco"
                  label="LOCO"
                  className="form-control"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="usd"
                  label="USD"
                  className="form-control"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="gold"
                  label="GOLD"
                  className="form-control"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-4"></div>
              <div className="col-lg-4">
                <Field
                  name="tanggal"
                  label="Tanggal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );
      case "saldoPiutang":
        return (
          <form>
            <div className="row">
              <div className="col-lg-4">
                <label htmlFor="">Jenis Laporan</label>
                <Field
                  name="jenisLaporan"
                  className="form-control"
                  component="select"
                >
                  <option value="CUSTOMER" key="CUSTOMER">
                    Per Customer
                  </option>
                  <option value="SEMUA" key="SEMUA">
                    SEMUA CUSTOMER
                  </option>
                </Field>
              </div>
              <div className="col-lg-4">
                <Field
                  name="namaCustomer"
                  label="nama Customer"
                  className="form-control"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-4">
                <div className="form-group">
                  <label htmlFor="">.</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="rekapCustomer"
                        component="input"
                        type="checkbox"
                        value="tanggal"
                        className="mr-2"
                      />
                      Rekap Customer
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="tanggal"
                  label="Tanggal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "titipanCustomer":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-5">
                <Field
                  name="langganan"
                  label="Langganan"
                  className="form-control"
                  component={renderField}
                  type="text"
                />
              </div>
              <div className="col-lg-2">
                <div className="form-group">
                  <label htmlFor="">.</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="jenisLangganan"
                        component="input"
                        type="checkbox"
                        value="tanggal"
                        className="mr-2"
                      />
                      Semua
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-5">
                <label htmlFor="">Lokasi</label>
                <Field
                  name="lokasi"
                  className="form-control"
                  component="select"
                >
                  <option value="SEMUA" key="SEMUA">
                    SEMUA
                  </option>
                </Field>
              </div>

              <div className="col-lg-4">
                <Field
                  name="tanggal"
                  label="Tanggal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      default:
        break;
    }
  }
}

FormCustomer = reduxForm({
  form: "FormCustomer",
  enableReinitialize: true,
})(FormCustomer);
const selector = formValueSelector("FormCustomer"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, StatusTitipan } = selector(
    state,
    "harga",
    "cash",
    "StatusTitipan"
  );
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    cashNettoMurni: parseFloat(cash),
    StatusTitipan: StatusTitipan,
  };
})(FormCustomer);
